<template>
  <v-container>
    <v-btn color="cred" @click="goBack" class="me-1"><v-icon>mdi-chevron-left</v-icon>Back</v-btn>
    <router-link to="/mywishlist">
      <v-btn color="info" class="me-1"><v-icon>mdi-heart-outline</v-icon>Add to Wishlist</v-btn>
    </router-link>
    <router-link to="">
      <v-btn color="success" class="me-1"><v-icon>mdi-map-marker</v-icon>Google Location</v-btn>
    </router-link>
    <v-row>
      <v-col cols="12" md="6">
        <v-card flat>
          <v-img v-if="sdetail.sale_imageurl==''" src="https://dummyimage.com/1500x1250/fd0606/fff&text=Sale" cover max-height="350" min-height="350" class="img-fluid"></v-img>
          <v-img v-if="sdetail.sale_imageurl!=''" :src=url+sdetail.sale_imageurl cover max-height="350" min-height="350" class="img-fluid"></v-img>
        </v-card>
      </v-col>
      <v-col cols="12" md="6">
        <div class="mobile"></div>
        <div class="desktop">
          <div class="px-md-5">
            <h2 class="h4 mb-0">{{sdetail.sale_title}}</h2>
            <div>By
              <router-link :to="{name:'businessprofile',params:{id:sdetail.user_id,uname:sdetail.username}}" class="text-decoration-none" title="Business Name">
                {{sdetail.name}}
              </router-link>
            </div>
            <div class="">Sale
              <del class=" ms-1 h6 text-muted">₹{{ sdetail.normal_price }}</del>
              <span class=" mx-1 h4 cred">₹{{ sdetail.sale_price }}</span>
              <span class="text-bg-light px-2 py-1 fs-6">{{Math.round(100-(100*sdetail.sale_price/sdetail.normal_price))}}% off</span>
            </div>
            <div><span class="fw-semibold">Validity: {{ sdetail.saledate_from }} - {{ sdetail.saledate_to }}</span></div>
            <div class="py-1">
              <a :href="('tel:'+ sdetail.mobile_phone)"><v-btn color="cred" class="me-1">
                <v-icon>mdi-phone</v-icon>
                {{ sdetail.mobile_phone }}</v-btn></a>
              <a :href="('https://wa.me/'+sdetail.mobile_phone+'?text=rf')"><v-btn color="success" class="me-1"><v-icon>mdi-whatsapp</v-icon>WhatsApp</v-btn></a>
            </div>
            <p class="px-1 text-justify">
              {{ sdetail.sale_description }} by {{sdetail.name}} from {{sdetail.city}}, {{sdetail.state}}
            </p>
          </div>
        </div>
      </v-col>
    </v-row>
    <v-row class="mt-10">

    </v-row>
  </v-container>
</template>

<script>
import axios from "axios";

export default {
  name: "SaleDetail",
  props:{
    id:Number
  },
  data(){
    return{
      url:'https://bizlx.itechvision.in',
      sdetail:[],
    }
  },
  methods:{
    getSaleDetail(){
      axios.get(`sales/detail/${this.id}`)
          .then((resp)=>{
            this.sdetail = resp.data.sdetail;
          });
    },
    goBack() {
      window.history.back();
    }
  },
  created() {
    this.getSaleDetail();
  }
}
</script>

<style scoped>

</style>